






























































import { AppService } from "@/service/app.service";
import { XHelper } from "@/x-vue-helper/x-helper";
import { PostModel } from "@/x-vue/interfaces/forum.interface";
import { CafeModel, RequestData } from "@/x-vue/interfaces/interfaces";
import { ApiService } from "@/x-vue/services/api.service";
import Vue from "vue";
import Component from "vue-class-component";
import CafeAdminMenu from "@/views/cafe/CafeAdminMenu.vue";

@Component({
  components: { CafeAdminMenu },
})
export default class CafePushMessage extends Vue {
  app = AppService.instance;
  cafe: CafeModel = {} as CafeModel;
  post: PostModel = {} as PostModel;
  tokenCount = 0;

  idx = "";
  title = "";
  body = "";

  loading = false;
  async mounted(): Promise<void> {
    // console.log("CafePushMessage::mounted(); ");

    let options: RequestData = {
      domain: this.$route.params.id,
    };

    try {
      this.cafe = await ApiService.instance.cafeGet(options);
      console.log("CafePushMessage", this.cafe);
    } catch (e) {
      XHelper.instance.error(e);
    }
  }

  async loadPostIdx(): Promise<void> {
    console.log(this.idx);
    if (this.idx === "") return;
    let options: RequestData = {
      idx: this.idx,
    };
    try {
      const post = await ApiService.instance.postGet(options);
      this.title = post.title;
      this.body = post.content;
      this.loading = false;
    } catch (e) {
      this.loading = false;
      XHelper.instance.error(e);
    }
  }

  async onSubmit(): Promise<void> {
    let options: RequestData = {
      domain: this.$route.params.id,
      title: this.title,
      body: this.body,
      idx: this.idx,
    };

    try {
      const res = await ApiService.instance.cafeSendMessage(options);
      console.log(res);
      XHelper.instance.alert("Send Push Message", "Success Sending push notification to topic.");
    } catch (e) {
      XHelper.instance.error(e);
    }
  }
}
